*:focus {
  outline: none !important;
}
.CartItem {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: row;
  width: 600px;
  height: 100px;
  margin: 0 auto;
  border: 1px solid orangered;
  margin-top: 20px;
  padding: 5px;
  background-color: white;
  position: relative;
  overflow: hidden;
}

.CartItem img {
  height: 90px;
  width: 90px;
  left: 5px;
  position: absolute;
}

.CartItem .about {
  position: absolute;
  left: 110px;
  width: 300px;
}
.count {
  display: flex;
  flex-direction: row;
  position: absolute;
  left: 70%;
}
.count button {
  width: 25px;
  height: 25px;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  margin-left: 3px;
}
.count p {
  margin-right: 3px;
  margin-left: 3px;
}

.price {
  display: flex;
  text-align: center;
  flex-direction: column;
  position: absolute;
  left: 85%;
}

.x {
  position: absolute;
  display: block;
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  border-width: 3px;
  border-style: solid;
  border-color: red;
  border-radius: 100%;
  background: -webkit-linear-gradient(
      -45deg,
      transparent 0%,
      transparent 46%,
      white 46%,
      white 56%,
      transparent 56%,
      transparent 100%
    ),
    -webkit-linear-gradient(45deg, transparent 0%, transparent 46%, white 46%, white
          56%, transparent 56%, transparent 100%);
  background-color: red;
  box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease;
  top: 4%;
  right: 1%;
  transition: all 0.2s ease-in-out;
}
.x:hover {
  transform: scale(1.2);
  cursor: pointer;
}

@media (max-width: 700px) {
  .CartItem {
    width: 90%;
  }
}
@media (max-width: 550px) {
  .CartItem {
    width: 97%;
  }
  .count {
    left: 53%;
    margin-top: 40px;
  }
  .price {
    left: 75%;
    margin-top: 40px;
  }
 .info{
 font-size: 15px;
 }
}
@media (max-width: 442px){
  .info{
    font-size: 13px;
    margin-top: 25px;
    }
    .price p{
      font-size: 15px;
    }
}
@media (max-width: 375px){
  .CartItem .about .info{
    font-size: 12px;
    margin-top: 25px;
    white-space: nowrap;
    text-overflow: ellipsis;
    }
    .price p{
      font-size: 15px;
    }
}